<template>
  <v-container>
    <v-row class="text-center">
      <v-col cols="12">
        <v-card>
          <v-card-title>
            Country list
            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Search"
              single-line
              hide-details
            ></v-text-field>
          </v-card-title>
      
          <v-data-table
            :headers="headers"
            :items="datas"
            :search="search"
            :footer-props="{
              'items-per-page-options': [20, 30, 40, 50, datas.length]
            }"
            @click:row="showCountry"
          >
            <template v-slot:item.flag="{ item }">
              <td class="twn-flag">
                <country-flag v-if="item.flag" :country='item.flag' size='normal'/>
              </td>
            </template>
          </v-data-table>
        </v-card>
      </v-col>
    </v-row>

    <v-row justify="center" v-if="selectedCountry != null">
      <v-dialog v-model="dialog" max-width="400">
        <v-card>
          <v-card-title class="headline">{{selectedCountry.name}}</v-card-title>
          <v-card-text>
            <div>
              <span class="twn-item-bold">Currencies: ({{selectedCountry.currencies.length}})</span>
              <ul v-for="currency in selectedCountry.currencies" :key="currency.code">
                <li>{{currency.code}}: {{currency.name}}</li>
              </ul>
            </div>
            
            <div>
              <span class="twn-item-bold">AltSpellings: ({{selectedCountry.altSpellings.length}})</span>
              <p v-if="selectedCountry.altSpellings.length < 1">
                No altSpelling.
              </p>
              <ul v-for="altSpelling in selectedCountry.altSpellings" :key="altSpelling">
                <li>{{altSpelling}}</li>
              </ul>
            </div>

            <div>
              <span class="twn-item-bold">Borders: ({{selectedCountry.borders.length}})</span>
              <p v-if="selectedCountry.borders.length < 1">
                No borders.
              </p>
              <ul v-for="border in selectedCountry.borders" :key="border">
                <li><a @click="showSpecificCountry(border)">{{border}}</a></li>
              </ul>
            </div>

          </v-card-text>
        </v-card>
      </v-dialog>
    </v-row>
  </v-container>
</template>

<script>

export default {
  name: 'CountryTable',
  props: {
    headers: Array,
    datas: Array,
    allCountries: Array
  },
  data() {
    return {
      rowsPerPageItems: [10, 20, 30, 40],
      dialog: false,
      selectedCountry: null,
      search: '',
      page: 1,
      pageCount: 0,
      itemsPerPage: 20
    }
  },
  methods: {
    showCountry(value) {
      this.dialog = true
      this.selectedCountry = this.getFullInfoCountry(value)[0]
    },
    showSpecificCountry(border) {
      this.selectedCountry = this.getFullInforCountryByBorder(border)[0]
    },
    getFullInforCountryByBorder(border) {
      return this.allCountries.filter(v => {
        if (v.alpha3Code == border) {
          return v
        }
      })
    },
    getFullInfoCountry(value) {
      return this.allCountries.filter(v => {
        if (v.name == value.name) {
          return v
        }
      })
    }
  }
}
</script>

<style lang="scss">
.twn-flag {
  border-bottom: none !important;
  padding: 0 !important;
}
.twn-item-bold {
  font-weight: bold;
}
</style>
