<template>
  <v-container>
    <v-overlay 
      v-if="areCountryLoading"
      :value="areCountryLoading"
    >
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
    
    <div v-else>
      <error-alert
        v-if="errorMessage != null"
        :message="errorMessage"
      />
      <country-table
        v-else
        :headers="countryTableHeaders"
        :datas="countryTableDatas"
        :allCountries="allCountries"
      />
    </div>
  </v-container>
</template>

<script>
import CountryTable from '../components/CountryTable'
import ErrorAlert from '../components/ErrorAlert'

export default {
  name: 'Index',
  components: {
    CountryTable,
    ErrorAlert
  },
  data() {
    return {
      countryTableHeaders: [
        {
          text: "Flag",
          align: "left",
          fixed: true,
          value: "flag",
          sortable: false
        },
        {
          text: "Name",
          fixed: true,
          align: "left",
          sortable: true,
          value: "name"
        },
        {
          text: "Capital",
          fixed: true,
          align: "left",
          sortable: true,
          value: "capital"
        },
        {
          text: "Continent",
          fixed: true,
          align: "left",
          sortable: true,
          value: "continent"
        }
      ],
      countryTableDatas: null,
      errorMessage: null,
      areCountryLoading: false,
      allCountries: []
    }
  },
  methods: {
    getAllCountry() {
      this.areCountryLoading = true

      this.axios.get('all').then(response => {
        this.countryTableDatas = response.data.map(v => {
          return {flag: v.alpha2Code, name: v.name || "NULL", capital: v.capital || "NULL", continent: v.region || "NULL"}
        })
        this.allCountries = response.data
        this.areCountryLoading = false
        return
      }).catch(e => {
        this.errorMessage = e
        this.error = true
        this.areCountryLoading = false
        return
      })
    }
  },
  created() {
    this.getAllCountry()
  }
}
</script>
